*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  font-size: 62.5%; // 1rem = 10px; 10px/16px = 62.5%

  @include respond(lg) {
    font-size: 56.25%; // 1rem = 9px, 9/16 = 56.25%
  }

  @include respond(md) {
    font-size: 50%; // 1rem = 8px, 8/16 = 50%
  }

  // @include respond(xl) {
  //   font-size: 75%; // 1rem = 12px, 12/16 = 75%
  // }
}
